import React from 'react'

class BioMerides extends React.Component{
    
    constructor(){
        super()
    }
        
    
    render(){
        return(
            <div className='bioMerides content'>
               <div className='logos'>
                    <img src={require('./images/eu_logo.png')} alt='eu logo'></img>
                    <img src={require('./images/msvvs_logo.png')} alt='msvvs logo'></img>
                    <img src={require('./images/opvai_logo.png')} alt='opvai logo'></img>
                    <img src={require('./images/vyskumna_agentura_logo.png')} alt='vyskumna agentura logo'></img>
                </div>
            </div>
        )
    }
}


    

export default BioMerides